body { font-family: Arial, Helvetica, sans-serif; background:#fafafa; color:#222; }
.container { max-width:640px; margin:28px auto; padding:12px; }
nav a { margin-right:10px; color:#0366d6; text-decoration:none; }
nav a:hover { text-decoration:underline; }
.card { display:flex; flex-direction:column; gap:8px; padding:14px; border:1px solid #e1e4e8; border-radius:8px; background:#fff; }
input { padding:8px; font-size:14px; border:1px solid #ccd0d5; border-radius:6px; }
label { font-size:13px; margin-top:6px; }
button { padding:8px 12px; font-size:14px; border-radius:6px; cursor:pointer; background:#0366d6; color:#fff; border:none; }
button:disabled { opacity:0.6; cursor:default; }
